.album-modal {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: start;
        align-items: flex-start;
    padding: 90px;
    background-color: rgba(0, 0, 0, 0.85);
    height: 100%;
}

.album-image-container {
    width: 30%;
    -ms-flex-negative: 0;
        flex-shrink: 0;
}

.album-modal__image {
    background-position: center center;
    background-repeat: no-repeat;
    width: 200px;
    height: 200px;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    background-size: cover;
}

.album-modal__info {
    padding-top: 10px;
    color: white;
    font-size: 20px;
}

.album-modal__info__album-name {
    font-size: 24px;
    color: white;
    text-transform: capitalize;
    font-weight: 800;
    margin-bottom: 10px;
}

.album-modal__info__count {
    color: rgb(216, 39, 39);
    font-weight: 800;
    width: 24px;
}

.album-modal__info__tracks-count {
    margin-bottom: 10px;
}

.album-modal__info__now-playing {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
}

.album-modal__info__now-playing>i {
    color: rgb(216, 39, 39);
    font-size: 24px;
    -ms-flex-negative: 0;
        flex-shrink: 0;
}

.album-modal__info__now-playing__name {
    width: 100%;
}

.song-list {
    width: 70%;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    height: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
}

.song {
    height: 45px;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    color: white;
    background-color: transparent;
    -webkit-transition-property: background-color;
    -o-transition-property: background-color;
    transition-property: background-color;
    -webkit-transition-duration: 250ms;
         -o-transition-duration: 250ms;
            transition-duration: 250ms;
    cursor: pointer;
}

.song:hover {
    background-color: rgba(255, 255, 255, 0.3);
}

.song:hover .song-button {
    color: rgb(216, 39, 39);
}

.song-button {
    width: 45px;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    font-size: 26px;
    -webkit-transition-property: color;
    -o-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 250ms;
         -o-transition-duration: 250ms;
            transition-duration: 250ms;
}

.song-name {
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
    -webkit-transition-duration: 250ms;
         -o-transition-duration: 250ms;
            transition-duration: 250ms;
    -webkit-transition-property: color;
    -o-transition-property: color;
    transition-property: color;
}

.song-name.active {
    color: rgb(216, 39, 39);
}

@-webkit-keyframes appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1
    }
}

@keyframes appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1
    }
}


